// Importa a função que inicializa o modal de configurações
import '../sass/main.scss';
import btnMenu from './modulos/btnMenu.js'
import { initModalConfig } from './modulos/modalConfig.js';

// Adiciona um evento para garantir que o DOM esteja totalmente carregado
document.addEventListener('DOMContentLoaded', () => {
    //Chama funçã btnMenu
    btnMenu();

    // Chama a função para iniciar toda a funcionalidade do modal 
    initModalConfig();
    
});

