
export default function btnMenu() {


  const btnMenu = document.getElementById('btnMenu');
  const menuLateral = document.getElementById('menuLateral'); //nav mobile
  const overlay = document.getElementById('overlay');
  const fecharMenu = document.getElementById('fecharMenu'); // X
  const menuItems = document.querySelectorAll('#menuLateral li')

  //ativa menu suspenso e seu overlay de tela
  btnMenu.addEventListener('click', () => {
    menuLateral.classList.toggle('ativo');
    overlay.classList.toggle('ativo');
  });

   // desativa o menu com um clique em qualquer item da lista
  menuItems.forEach(item => {
    item.addEventListener('click', () => {
      menuLateral.classList.remove('ativo');
      overlay.classList.remove('ativo');
    });
  })

  //remove menu suspenso e seu overlay de tela
  fecharMenu.addEventListener('click', () => {
    menuLateral.classList.remove('ativo');
    overlay.classList.remove('ativo');
  });

  //remove menu suspenso e seu overlay de tela
  overlay.addEventListener('click', () => {
    menuLateral.classList.remove('ativo');
    overlay.classList.remove('ativo');
  });
};



      