// Função para inicializar a lógica do modal
export function initModalConfig() {
  // === Elementos HTML do Modal ===
  const btnConfiguracoes = document.getElementById("btnConfiguracoes");
  const modalConfiguracoes = document.getElementById("modalConfiguracoes");
  const btnFecharModal = modalConfiguracoes.querySelector(".btn-fechar");
  
  // === Elementos da Janela de Configurações ===
  const btnMudarFundo = document.getElementById("btnMudarFundo");
  
  // === Elementos Globais ===
  const root = document.documentElement;

  // Função para abrir o modal
  function abrirModal() {
    modalConfiguracoes.style.display = "flex";
  }

  // Função para fechar o modal
  function fecharModal() {
    modalConfiguracoes.style.display = "none";
  }

  // === Event Listeners (Ouvintes de Eventos) ===

  // 1. Botão de engrenagem abre o modal
  if (btnConfiguracoes) {
    btnConfiguracoes.addEventListener("click", abrirModal);
  }

  // 2. Botão de fechar e clique no fundo fecham o modal
  if (btnFecharModal) {
    btnFecharModal.addEventListener("click", fecharModal);
  }
  
  // Fecha o modal ao clicar fora da área de conteúdo
  if (modalConfiguracoes) {
    modalConfiguracoes.addEventListener("click", (event) => {
      // Verifica se o clique foi diretamente no fundo (e não no conteúdo do modal)
      if (event.target === modalConfiguracoes) {
        fecharModal();
      }
    });
  }

  // 3. Botão "Mudar Fundo" aciona a troca de cor
  if (btnMudarFundo) {
    btnMudarFundo.addEventListener("click", () => {
      const corAtual = window.getComputedStyle(root).getPropertyValue('--corFundo').trim();
      const corCreme = window.getComputedStyle(root).getPropertyValue('--corCreme').trim();
      const corAzeitona = window.getComputedStyle(root).getPropertyValue('--corAzeitona').trim();
  
      if (corAtual === corCreme) {
        root.style.setProperty('--corFundo', 'var(--corAzeitona)');
      } else {
        root.style.setProperty('--corFundo', 'var(--corCreme)');
      }
    });
  }
}